import { Injectable, EventEmitter } from '@angular/core';
import {Recipe} from "./recipe";
import {Ingredient} from "../ingredient";
import {Headers, Http, Response} from "@angular/http";
import 'rxjs/Rx';

@Injectable()
export class RecipeService {
  recipesChanged = new EventEmitter<Recipe[]>();
  recipes: Recipe[] = [
    new Recipe('Schnitzel','Very tasty','http://www.seriouseats.com/images/2014/04/20140410-289531-chicken-schnitzel.jpg',[
      new Ingredient('French Fries', 2),
      new Ingredient('Pork Meat', 1)
    ]),
    new Recipe('Summer Salad','Okayish','http://cdn.iowagirleats.com/wp-content/uploads/2013/05/Triple-Berry-Summer-Salad-03_mini.jpg',[
      new Ingredient('Lettuce', 2),
      new Ingredient('Tofu', 1)
    ]),
    new Recipe('Paneer Tikka','Cottage Cheese with marinated Indian spices','http://www.vegrecipesofindia.com/wp-content/uploads/2011/10/tandoori-paneer-tikka.jpg',[
      new Ingredient('Cottage Cheese', 2),
      new Ingredient('Indian Spices ', 1)
    ]),
    new Recipe('Biryani','Real Spicy','http://www.gharanarestaurant.com/wp-content/uploads/2015/06/Lucknowi-Biryani.jpg',[
      new Ingredient('Basmati Rice', 2),
      new Ingredient('Shaan Biryani Mix', 1)
    ])
  ];
  constructor(private http: Http) { }

  getRecipe() {
    return this.recipes;
  }

  getRecipes(id: number) {
    return this.recipes[id];
  }

  deleteRecipe(recipe: Recipe) {
    this.recipes.splice(this.recipes.indexOf(recipe), 1);
  }

  addRecipe(recipe: Recipe) {
    this.recipes.push(recipe);
  }

  editRecipe(oldRecipe: Recipe, newRecipe: Recipe) {
    this.recipes[this.recipes.indexOf(oldRecipe)] = newRecipe;
  }

  storeData() {
    const body = JSON.stringify(this.recipes);
    const headers = new Headers({
      'Content-Type' : 'application/json'
    });
    return this.http.put('https://recipebook-88b00.firebaseio.com/recipes.json', body, {
      headers: headers
    });
  }

  fetchData() {
    return this.http.get('https://recipebook-88b00.firebaseio.com/recipes.json')
      .map((response: Response) => response.json())
      .subscribe(
        (data: Recipe[]) => {
          this.recipes = data;
          this.recipesChanged.emit(this.recipes);
        }
      );
  }
 }



